// Modules
@import "modules/variables";

// Partials
@import "partials/reset";
@import "partials/typography";
@import "partials/menu";

// Vendor
@import "vendor/slideout";

// Hero

.title{
	font-size: 20px;
}
.header{
	padding: 40px 0 69px 0;
	height: 100vh;
	background: #222 url("../img/contacts-bg.png") no-repeat right 150px / auto 70vh;

	.heading{
		display: grid;
		grid-template-columns: 1fr 4fr 1fr;

		.nav{
			margin-left: 0;
		}

		.menu_bars{
			grid-column: 3/4;
		}

		.offer{
			grid-column: 2/3;
			margin-top: 141px;

			&__subtitle{
				line-height: 1.8em;
				margin-top: 23px;
				margin-bottom: 136px;
			}
		}

		.info{
			grid-column: 2/4;

			.schedule{
				caption{
					text-align: left;
					margin-bottom: 20px;
				}

				tr td:last-child{
					text-align: right;
				}

				width: 225px;
				margin-right: 89px;
			}

			.tel{
				width: 217px;
				margin-left: 102px;

				.title{
					line-height: 34px;
					margin-bottom: 11.6px;
				}
			}
		}

		.letter{
			grid-column: 2/4;
			margin-top: 96px;
			width: 535px;

			&__title{
				margin-bottom: 19px;
			}
		}
	}
}

// Responsive
@import "partials/responsive/general-responsive";
@import "partials/responsive/contacts-responsive";