// Extra large devices (large desktops)
// No media query since the extra-large breakpoint has no upper bound on its width

// Large devices (desktops, less than 1722px)
@media (max-width: 1722px) {
	.title{
		font-size: 16px;
	}
	.header{
		background-size: 635px 573px;

		.heading{
			.offer{
				margin-top: 97px;

				&__subtitle{
					margin-top: 52px;
					margin-bottom: 70px;
				}
			}

			.info{
				.schedule{
					caption{
						margin-bottom: 12px;
					}

					margin-right: 45px;
				}

				.tel{
					margin-left: 99px;

					.title{
						line-height: 21px;
						margin-bottom: 10px;
					}
				}
			}

			.letter{
				margin-top: 65px;
				width: 464px;

				&__title{
					margin-bottom: 13px;
				}
			}
		}
	}
}

// Medium devices (tablets, less than 992px)
@media (max-width: 992px) {
	.header{
		padding-bottom: 431px;
		background-size: 100%;
		background-position: center 90vw;
		min-height: 960px;

		h1, h2, h3{
			font-weight: 400;
		}

		.heading{
			.nav__menu{
				justify-content: center;
			}
			.offer, .info, .letter{
				grid-column: 1/3;
			}
			.letter{
				width: 100%;
			}
		}
	}
}

// Small devices (landscape phones, less than 768px)
@media (max-width: 768px) {
	.title{
		font-size: 12px;
	}
	.header{
		background-size: auto 431px;
    	background-position: center bottom;
	}
	.header .heading{
		.offer, .info, .letter{
			grid-column: 1/4;
		}

		.info{
			& > * {
				width: 50% !important;
			}
			.tel{
				margin-left: 36px;
			}
		}
	}
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 576px) {
	.header{
		padding-bottom: 0;
		background-size: center;

		.heading{
			.offer{
				margin-top: 52px;

				&__subtitle{
					margin-top: 10px;
    				margin-bottom: 43px;
				}
			}

			.letter{
				margin-top: 41px;
			}
		}
	}
	.header .heading .info .schedule{
		margin-right: 0;
	}
}

@media (min-width: 1024px){
	.header{
		min-height: 900px;
	}
} 